// @flow
import { SET_CREATE_LINK } from '../actions/links';
import type { Action, PageStatusModel } from './types';

const initial: PageStatusModel = {
  status: 0,
  type: '',
  description: ''
};
export default function linkcreation(
  state: PageStatusModel = initial,
  action: Action
) {
  switch (action.type) {
    case SET_CREATE_LINK:
      return Object.assign({}, action.payload);
    default:
      return state;
  }
}
