// @flow
/* eslint-disable camelcase */
/* eslint-disable no-unused-vars */
/* eslint-disable no-await-in-loop */

import React, { Component } from 'react';
import {
  Layout,
  Table,
  Badge,
  Menu,
  Dropdown,
  Icon,
  Row,
  Col,
  Button,
  Alert,
  Popconfirm
} from 'antd';
import { ColumnProps } from 'antd/lib/table';
import { remote, ipcRenderer } from 'electron';

import styles from './LinksTable.scss';

import type {
  LinkInforModel,
  SettingModel,
  PageStatusModel,
  LinkDetailModel
} from '../reducers/types';
import {
  getProxyLinksByPage,
  getProxyLinkDetail,
  getRules,
  getProxyLink,
  deactiveProxylink
} from '../actions/links';
import { openNotificationWithIcon, RuleTable } from './Extra_components';
import LinkDataModal from './LinkDataModal';

const { Header, Footer, Sider, Content } = Layout;
const { BrowserWindow, dialog } = remote;

type Props = {
  settings: SettingModel
};
type State = {
  linksArray: LinkInforModel[],
  pagestatus: PageStatusModel,
  isInjecting: boolean,
  linkscount: number,
  pageIndex: number,
  pageSize: number,
  linkDataModalVisible: boolean,
  selectedLinkID: number,

  isGoingtoDeactive: boolean
};

const columns: ColumnProps<LinkInforModel>[] = [
  {
    key: 'containingStatus',
    title: '',
    dataIndex: 'link_detail_status',
    render: link_detail_status => {
      switch (link_detail_status) {
        case 2:
          return <Badge status="success" />;
        case 1:
          return <Badge status="default" />;
        default:
          return <Badge status="error" />;
      }
    }
  },
  {
    key: 'status',
    title: 'Status',
    dataIndex: 'is_active',
    render: is_active => {
      if (is_active) return <span>Active</span>;
      return <span>Inactive</span>;
    }
  },
  {
    key: 'service',
    title: 'Service',
    dataIndex: 'service'
  },
  {
    key: 'created',
    title: 'Created',
    dataIndex: 'created'
  },
  {
    key: 'short_code',
    title: 'Short Code',
    dataIndex: 'short_code'
  },
  {
    key: 'description',
    title: 'Description',
    dataIndex: 'description'
  },
  {
    key: 'action',
    title: 'Deactive',
    render: record => {
      if (record.is_active)
        return (
          <Button className="deactiveButton">
            <Icon type="stop" />
          </Button>
        );
      return (
        <Button disabled>
          <Icon type="stop" />
        </Button>
      );
    }
  }
];

// check Containing Cookies.....
const isContainCookies = (linkDetail: LinkDetailModel[]) => {
  for (let i = 0; i < linkDetail.length; i += 1)
    if (linkDetail[i].cookies !== null) return true;

  return false;
};

export default class LinksTable extends Component<Props, State> {
  props: Props;

  state: State;

  constructor(props: Props) {
    super(props);
    this.state = {
      linksArray: [],
      pagestatus: { status: 0, type: '', description: '' },
      linkscount: 0,
      pageIndex: 0,
      pageSize: 0,
      isInjecting: false,
      isGoingtoDeactive: false,
      linkDataModalVisible: false,
      selectedLinkID: null
    };

    this.onChagePagination = this.onChagePagination.bind(this);
    this.onLinksTable = this.onLinksTable.bind(this);
    this.onCloseLinkDataModal = this.onCloseLinkDataModal.bind(this);
    this.onGetAllProxyLinks = this.onGetAllProxyLinks.bind(this);
    this.onDeactiveProxyLink = this.onDeactiveProxyLink.bind(this);
    this.onInjection = this.onInjection.bind(this);
  }

  componentDidMount = async () => {
    const { pagestatus } = this.state;
    pagestatus.status = 1;
    this.setState({
      pagestatus
    });
    await this.onGetAllProxyLinks(1);
    pagestatus.status = 0;
    this.setState({
      pagestatus
    });
  };

  onChagePagination = async (page: number, _pageSize: number) => {
    const { pagestatus } = this.state;
    pagestatus.status = 1;
    this.setState({
      pagestatus
    });
    await this.onGetAllProxyLinks(page);
    pagestatus.status = 0;
    this.setState({
      pagestatus
    });
  };

  onGetAllProxyLinks = async (curPage: number) => {
    const { settings } = this.props;
    try {
      const linksData = await getProxyLinksByPage(settings, curPage);

      const linkscount: number = linksData.count;
      const pageIndex: number = linksData.current_page;
      const pageSize: number = linksData.page_size;
      const linksArray: Array<LinkInforModel> = [];

      for (let i = 0; i < linksData.results.length; i += 1) {
        const item = linksData.results[i];

        const ruleData = [];

        item.rules.forEach(async rule => {
          const rule_data = await getRules(settings, '', rule.id);
          ruleData.push(rule_data);
        });

        // get all link details

        const { count, results } = await getProxyLinkDetail(settings, item.id);

        let link_detail_status = 0;
        if (count !== 0) {
          link_detail_status = 1 + isContainCookies(results);
        }

        const newLinkInforModel: LinkInforModel = {
          id: item.id,
          service: item.original_host,
          created: item.created,
          is_active: item.is_active,
          short_code: item.shortcode,
          description: item.description,
          rule_data: RuleTable(ruleData),
          link_detail_status
        };

        linksArray.push(newLinkInforModel);
      }

      this.setState({
        linkscount,
        pageIndex,
        pageSize,
        linksArray
      });
    } catch (e) {
      const notification: PageStatusModel = {
        status: 0,
        type: 'error',
        description:
          'Something went wrong. Please check your credentials or Sproot API URL in the settings.'
      };
      openNotificationWithIcon(notification);
    }
  };

  onLinksTable = (record: LinkInforModel, rowIndex: number) => {
    const { settings } = this.props;
    const link_id = record.id;
    return {
      onClick: event => {
        if (event.target.getAttribute('class') === 'ant-btn deactiveButton') {
          const options = {
            buttons: ['Yes', 'No'],
            message: `Are you sure you want to deactive ${link_id} `
          };
          dialog.showMessageBox(options, async response => {
            if (response === 0) {
              this.setState({ isGoingtoDeactive: true });
              await this.onDeactiveProxyLink(link_id, rowIndex);
            }
          });
        }
        // else if (record.link_detail_status === 0) {
        //   const notification: PageStatusModel = {
        //     status: 0,
        //     type: 'error',
        //     description: `No data available. Link hasn't been accessed`
        //   };
        //   openNotificationWithIcon(notification);
        // }
        else {
          this.setState({
            linkDataModalVisible: true,
            selectedLinkID: link_id
          });
        }
      }
    };
  };

  onCloseLinkDataModal = () => {
    this.setState({
      linkDataModalVisible: false,
      selectedLinkID: null
    });
  };

  onInjection = async (linkData: LinkDetailModel) => {
    this.setState({
      isInjecting: true
    });
    const { settings } = this.props;
    const { linksArray, selectedLinkID } = this.state;
    const currenLink: LinkInforModel = linksArray.filter(
      ele => ele.id === selectedLinkID
    )[0];

    console.log('Going to inject cookies?', linkData);
    ipcRenderer.send('runCommand', settings, linkData, currenLink.service);
    this.setState({
      isInjecting: false
    });
  };

  onDeactiveProxyLink = async (removeId: number, index: number) => {
    const { settings } = this.props;
    const { linksArray } = this.state;

    linksArray[index].is_active = false;
    this.setState({ isGoingtoDeactive: true, linksArray });
    const proxyLinkData = await getProxyLink(settings, removeId);
    const {
      rules,
      domains,
      original_host,
      on_error_redirect,
      is_active
    } = proxyLinkData;
    const newLinkData = {
      rules,
      domains,
      original_host,
      on_error_redirect,
      is_active: !is_active
    };
    await deactiveProxylink(settings, newLinkData, removeId);
    this.setState({ isGoingtoDeactive: false });
    this.forceUpdate();
  };

  render() {
    const {
      pagestatus,
      linksArray,
      pageSize,
      linkscount,
      pageIndex,
      linkDataModalVisible,
      selectedLinkID,
      isInjecting,
      isGoingtoDeactive
    } = this.state;

    const { settings } = this.props;
    return (
      <Layout>
        <Content>
          <Table
            loading={pagestatus.status === 1 || isGoingtoDeactive}
            columns={columns}
            dataSource={linksArray}
            rowKey={record => record.id}
            pagination={{
              pageSize,
              total: linkscount,
              current: pageIndex,
              onChange: this.onChagePagination
            }}
            expandedRowRender={record => record.rule_data}
            size="small"
            onRow={this.onLinksTable}
            bordered
            className={styles.linksTable}
          />

          {linkDataModalVisible && (
            <LinkDataModal
              key={selectedLinkID}
              title={`${selectedLinkID} Link Details`}
              visible={linkDataModalVisible}
              onOk={this.onCloseLinkDataModal}
              onCancel={this.onCloseLinkDataModal}
              linkID={selectedLinkID}
              onInjection={this.onInjection}
              settings={settings}
              isInjecting={isInjecting}
            />
          )}
        </Content>
      </Layout>
    );
  }
}
