// @flow

import {
  SET_PROXY_PROVIDER,
  SET_PROXIES,
  SET_PROXY_PROVIDER_LOADING,
  SET_PROXIES_LOADING,
  SET_SELECTED_PROXY_PROVIDER
} from '../actions/proxy';
import type { Action, ProxyState } from './types';

const initial: ProxyState = {
  providers: [],
  proxies: [],
  isProxyProviderLoading: false,
  isProxiesLoading: false,
  selectedProviderID: -1
};

export default function proxy(state: ProxyState = initial, action: Action) {
  let newState: ProxyState;
  switch (action.type) {
    // Proxy Provider
    case SET_PROXY_PROVIDER_LOADING:
      newState = Object.assign({}, state);
      newState.isProxyProviderLoading = true;
      return newState;
    case SET_PROXY_PROVIDER:
      newState = Object.assign({}, state);
      newState.providers = action.payload.filter(
        element => element.provider === 'other'
      );
      newState.proxies = [];
      newState.selectedProviderID = -1;
      newState.isProxyProviderLoading = false;
      return newState;
    case SET_SELECTED_PROXY_PROVIDER: {
      const { providers } = state;
      const newProviders = [];
      providers.forEach(element => {
        const newElement = element;
        newElement.is_selected = newElement.id === action.payload;
        newProviders.push(newElement);
      });
      newState = Object.assign({}, state);
      newState.selectedProviderID = action.payload;
      newState.providers = newProviders;
      return newState;
    }
    //  Proxy Server
    case SET_PROXIES:
      newState = Object.assign({}, state);
      newState.proxies = action.payload;
      newState.isProxiesLoading = false;
      return newState;
    case SET_PROXIES_LOADING:
      newState = Object.assign({}, state);
      newState.isProxiesLoading = true;
      return newState;
    default:
      return state;
  }
}
