import React, { Component } from 'react';
import { ColumnProps } from 'antd/lib/table';
import { remote } from 'electron';
import {
  Layout,
  Icon,
  Row,
  Modal,
  Table,
  Tabs,
  Select,
  Button,
  Spin,
  Pagination,
  List,
  Col
} from 'antd';
import type {
  PageStatusModel,
  LinkDetailModel,
  SettingModel,
  ShortLinkInforModel
} from '../reducers/types';
import styles from './LinkDataModal.scss';

import {
  getProxyLinkDetail,
  getProxyLink,
  getRules,
  getShortLinkByRuleID
} from '../actions/links';
// import { getCountryName, encodedUrl } from '../utils';
import { openNotificationWithIcon, RuleTable } from './Extra_components';

const { TabPane } = Tabs;
const { BrowserWindow, dialog } = remote;

const antIcon = <Icon type="loading" style={{ fontSize: 48 }} spin />;
const columns: ColumnProps<ShortLinkInforModel>[] = [
  {
    key: 'status',
    title: 'Status',
    dataIndex: 'is_active',
    render: is_active => {
      if (is_active) return <span style={{ color: 'lightgreen' }}>Active</span>;
      return <span style={{ color: 'lightred' }}>Inactive</span>;
    }
  },
  {
    key: 'created',
    title: 'Created',
    dataIndex: 'created'
  },
  {
    key: 'access_urls',
    title: 'URL',
    dataIndex: 'access_urls',
    render: access_urls => <a>{access_urls}</a>
  },
  {
    key: 'on_error_redirect',
    title: 'Error Redirect URL',
    dataIndex: 'on_error_redirect'
  },
  {
    key: 'description',
    title: 'Description',
    dataIndex: 'description'
  },
  {
    key: 'action',
    title: 'Disable',
    render: record => {
      if (record.is_active)
        return (
          <Button className="deactiveButton">
            <Icon type="stop" />
          </Button>
        );
      return (
        <Button disabled>
          <Icon type="stop" />
        </Button>
      );
    }
  }
];
// LinkDetailData Modal
type LinkDataModalProps = {
  title: string,
  visible: boolean,
  linkID: number,
  // function
  onOk: any,
  onCancel: any,
  onInjection: any,
  settings: SettingModel,
  isInjecting: boolean
};

type LinkDataModalState = {
  linkData: LinkDetailModel[],
  count: number,
  selectedDetail: number,
  pagestatus: PageStatusModel,
  shortLinks: ShortLinkInforModel[],
  isGoingtoDeactive: boolean
};

const convertText = data => {
  if (!data) return 'Null';
  return data.toString();
};

export default class LinkDataModal extends Component<
  LinkDataModalProps,
  LinkDataModalState
> {
  props: LinkDataModalProps;

  state: LinkDataModalState;

  constructor(props: LinkDataModalProps) {
    super(props);
    this.state = {
      linkData: [],
      count: 0,
      selectedDetail: 1,
      pagestatus: { status: 0, type: '', description: '' },
      shortLinks: [],
      isGoingtoDeactive: false
    };

    this.onClickInject = this.onClickInject.bind(this);
    this.onSelectDetail = this.onSelectDetail.bind(this);
    this.onLinksTable = this.onLinksTable.bind(this);
  }

  componentDidMount = async () => {
    const { linkID, settings } = this.props;
    try {
      const { pagestatus } = this.state;
      pagestatus.status = 1;
      this.setState({
        pagestatus
      });

      // BEGIN: GET Proxy Detail
      console.log('BEGIN: GET Proxy Detail');
      const response = await getProxyLinkDetail(settings, linkID);
      const { count, total_pages, results } = response;

      for (let i = 2; i <= total_pages; i += 1) {
        const newResponse = await getProxyLinkDetail(settings, linkID, i);
        results.concat(newResponse.results);
      }
      // END: GET Proxy Detail

      // BEGIN: get rule id and short link
      console.log('BEGIN: get rule id and short link');
      const proxyLink = await getProxyLink(settings, linkID);
      const proxyAccessUrl: string[] = proxyLink.access_urls;

      let shortLinks: ShortLinkInforModel[] = [];
      const rulesforProxy = await getRules(settings, proxyAccessUrl[0]); // Get rules by access_url.
      //   console.log(rulesforProxy);
      for (let i = 0; i < rulesforProxy.results.length; i += 1) {
        const rule = rulesforProxy.results[i];
        const tempResponse = await getShortLinkByRuleID(settings, rule.id); // Short link by rule_id.

        for (let j = 0; j < tempResponse.results.length; j += 1) {
          const res = tempResponse.results[j];

          const ruleData = [];
          for (let k = 0; k < res.rules.length; k += 1) {
            const shortLinkRule = res.rules[k];
            const rulesforShortLink = await getRules(
              settings,
              '',
              shortLinkRule.id
            );
            ruleData.push(rulesforShortLink);
          }
          const extraction: ShortLinkInforModel = {
            id: res.id,
            created: res.created,
            modified: res.modified,
            on_error_redirect: res.on_error_redirect,
            description: res.description,
            rule_data: RuleTable(ruleData, 'calc(90vw - 320px)'),
            is_active: res.is_active,
            short_code: res.shortcode,
            access_urls: res.access_urls
          };
          shortLinks.push(extraction);
        }
      }

      console.log(shortLinks);
      // END: get rule id and short link

      pagestatus.status = 0;
      this.setState({
        linkData: results,
        count,
        pagestatus,
        shortLinks
      });
    } catch (e) {
      const notify: PageStatusModel = {
        status: 0,
        type: 'error',
        description:
          'Something went wrong. Please check your credentials or Sproot API URL in the settings.'
      };
      openNotificationWithIcon(notify);
    }
  };

  onClickInject = () => {
    const { onInjection } = this.props;
    const { linkData, selectedDetail } = this.state;

    onInjection(linkData[selectedDetail - 1]);
  };

  onSelectDetail = (pageNum: number) => {
    this.setState({
      selectedDetail: pageNum
    });
  };

  onLinksTable = (record: ShortLinkInforModel, rowIndex: number) => {
    const { settings } = this.props;
    const link_id = record.id;
    console.log(link_id);
    return {
      onClick: event => {
        if (event.target.getAttribute('class') === 'ant-btn deactiveButton') {
          const options = {
            buttons: ['Yes', 'No'],
            message: `Are you sure you want to deactive ${link_id} `
          };
          dialog.showMessageBox(options, async response => {
            if (response === 0) {
              this.setState({ isGoingtoDeactive: true });
              await this.removeShortLink(link_id);
            }
          });
        }
      }
    };
  };

  removeShortLink = async (removeId: number) => {
    alert(`ok go to remove ${removeId}`);
    this.setState({ isGoingtoDeactive: false });
  };

  render() {
    const { title, visible, onOk, onCancel, linkID, isInjecting } = this.props;
    const {
      linkData,
      pagestatus,
      count,
      selectedDetail,
      shortLinks
    } = this.state;
    console.log(linkID, linkData);
    if (!linkID || pagestatus.status === 1) {
      return (
        <Modal title={title} onOk={onOk} onCancel={onCancel} visible={visible}>
          <div style={{ textAlign: 'center' }}>
            <Spin indicator={antIcon} />
          </div>
        </Modal>
      );
    }

    if (linkData.length === 0) {
      return (
        <Modal
          title={[
            <React.Fragment>
              <Row style={{ fontSize: '24px', color: 'rgb(0, 0, 0, 0.5)' }}>
                {title}
              </Row>
            </React.Fragment>
          ]}
          onOk={onOk}
          onCancel={onCancel}
          visible={visible}
          width="90vw"
          footer={[
            <Button key="back" onClick={onOk}>
              Close
            </Button>
          ]}
        >
          <React.Fragment>
            <Tabs tabPosition="left">
              {shortLinks.length && (
                <TabPane tab="Short Links" key="1">
                  <Table
                    columns={columns}
                    dataSource={shortLinks}
                    rowKey={record => record.id}
                    expandedRowRender={record => record.rule_data}
                    size="small"
                    bordered
                    onRow={this.onLinksTable}
                    className={styles.linksTable}
                  />
                </TabPane>
              )}
              {shortLinks.length === 0 && (
                <TabPane tab="Short Links" key="1">
                  <span>No short Links</span>
                </TabPane>
              )}
            </Tabs>
          </React.Fragment>
        </Modal>
      );
    }

    const { cookies } = linkData[selectedDetail - 1];
    const {
      ip_address,
      user_agent,
      other,
      is_cscript,
      is_bot,
      created,
      modified
    } = linkData[selectedDetail - 1].session;

    return (
      <Modal
        title={[
          <React.Fragment key={selectedDetail}>
            <Row style={{ fontSize: '24px', color: 'rgb(0, 0, 0, 0.5)' }}>
              {title}
            </Row>
            <Row>
              <Pagination
                key={selectedDetail}
                showQuickJumper
                defaultCurrent={selectedDetail}
                total={count}
                pageSize={1}
                onChange={this.onSelectDetail}
                style={{ float: 'right' }}
              />
            </Row>
          </React.Fragment>
        ]}
        onOk={onOk}
        onCancel={onCancel}
        visible={visible}
        width="90vw"
        footer={[
          <Button key="back" onClick={onOk}>
            Close
          </Button>,
          <Button
            key="inject"
            type="primary"
            disabled={cookies === null}
            onClick={this.onClickInject}
            loading={isInjecting}
          >
            Inject
          </Button>
        ]}
      >
        <React.Fragment>
          <Tabs tabPosition="left">
            {shortLinks.length && (
              <TabPane tab="Short Links" key="1">
                <Table
                  columns={columns}
                  dataSource={shortLinks}
                  rowKey={record => record.id}
                  expandedRowRender={record => record.rule_data}
                  size="small"
                  bordered
                  onRow={this.onLinksTable}
                  className={styles.linksTable}
                />
              </TabPane>
            )}
            {shortLinks.length === 0 && (
              <TabPane tab="Short Links" key="1">
                <span>No short Links</span>
              </TabPane>
            )}
            <TabPane tab="Global" key="2">
              <span>is_cscript:</span> {convertText(is_cscript)}
              <br />
              <span>is_bot:</span> {convertText(is_bot)}
              <br />
              <span>created:</span> {convertText(created)}
              <br />
              <span>modified:</span> {convertText(modified)}
            </TabPane>
            <TabPane tab="IP Address" key="3">
              <span>IP Address:</span> {convertText(ip_address.ip_address)}
              <br />
              <span>Created Date:</span> {convertText(ip_address.created)}
              <br />
              <span>Modified Date:</span> {convertText(ip_address.modified)}
              <br />
              <span>Country:</span> {convertText(ip_address.country_name)}
              <br />
              <span>Region:</span> {convertText(ip_address.region_name)}
              <br />
              <span>City:</span> {convertText(ip_address.city)}
              <br />
              <span>Zip Code:</span> {convertText(ip_address.zip_code)}
              <br />
              <span>Time Zone:</span> {convertText(ip_address.time_zone)}
              <br />
              <span>Latitude:</span> {convertText(ip_address.latitude)}
              {'        '}
              <span>Longitude:</span> {convertText(ip_address.longitude)}
              <br />
              <span>Metro Code:</span> {convertText(ip_address.metro_code)}
              <br />
              <span>Organization:</span> {convertText(ip_address.organization)}
              <br />
              <span>Autonomous System Organization:</span>{' '}
              {convertText(ip_address.autonomous_system_organization)}
              <br />
              <span>Autonomous System Number:</span>{' '}
              {convertText(ip_address.autonomous_system_number)}
              <br />
              <span>ISP:</span> {convertText(ip_address.isp)}
              <br />
              <span>Blocked?:</span> {convertText(ip_address.is_blocked)}
            </TabPane>
            <TabPane tab="User Agent" key="4">
              <span>User Agent:</span> {convertText(user_agent.user_agent)}
              <br />
              <span>Created Date:</span> {convertText(user_agent.created)}
              <br />
              <span>Modified Date:</span> {convertText(user_agent.modified)}
              <br />
              <span>Bot?:</span> {convertText(user_agent.is_bot)}
              <br />
              <span>Device Brand:</span> {convertText(user_agent.device_brand)}
              <br />
              <span>Device Family:</span>{' '}
              {convertText(user_agent.device_family)}
              <br />
              <span>Device Model:</span> {convertText(user_agent.device_model)}
              <br />
              <span>OS Family:</span> {convertText(user_agent.os_family)}
              <br />
              <span>OS Major:</span> {convertText(user_agent.os_major)}
              <br />
              <span>OS Minor:</span> {convertText(user_agent.os_minor)}
              <br />
              <span>OS Patch:</span> {convertText(user_agent.os_patch)}
              <br />
              <span>OS Patch Minor:</span>{' '}
              {convertText(user_agent.os_patch_minor)}
              <br />
              <span>Browser Family:</span>{' '}
              {convertText(user_agent.browser_family)}
              <br />
              <span>Browser Major:</span>{' '}
              {convertText(user_agent.browser_major)}
              <br />
              <span>Browser Minor:</span>{' '}
              {convertText(user_agent.browser_minor)}
              <br />
              <span>Browser Patch:</span>{' '}
              {convertText(user_agent.browser_patch)}
              <br />
              <span>Mobile?:</span> {convertText(user_agent.is_mobile)}
              <br />
              <span>Tablet?:</span> {convertText(user_agent.is_tablet)}
              <br />
              <span>PC?:</span> {convertText(user_agent.is_pc)}
              <br />
              <span>Touch Capable:</span>{' '}
              {convertText(user_agent.is_touch_capable)}
            </TabPane>
            {other && (
              <TabPane tab="Other" key="5">
                <span>Timezone</span> {convertText(other.timezone)}
                <br />
                <span>BrowserLanguage</span>{' '}
                {convertText(other.browserLanguage)}
                <br />
                <span>BrowserPlatform</span>{' '}
                {convertText(other.browserPlatform)}
                <br />
                <span>BrowserScreenWidth</span>{' '}
                {convertText(other.browserScreenWidth)}
                <br />
                <span>BrowserScreenHeight:</span>{' '}
                {convertText(other.browserScreenHeight)}
              </TabPane>
            )}
          </Tabs>
        </React.Fragment>
      </Modal>
    );
  }
}
