// @flow
import { SET_SETTINGS_DATA } from '../actions/settings';
import type { Action, SettingModel } from './types';

const initial: SettingModel = {
  credential: {
    username: '',
    password: ''
  },
  sproot_config: {
    url_sproot_api: '',
    sfqdn: [],
    lfqdn: [],
    default_human_red_url: '',
    default_bot_red_url: '',
    default_error_red_url: ''
  },
  extra_urls: {
    url_charge_api: '',
    url_extraction_api: ''
  },
  default_proxy_config: {
    proxy_type: '',
    proxy_address: '',
    port: '',
    credential: {
      username: '',
      password: ''
    }
  },
  access_token: '',
  pagesatus: {
    status: 0,
    type: '',
    description: ''
  }
};
export default function settings(
  state: SettingModel = initial,
  action: Action
) {
  switch (action.type) {
    case SET_SETTINGS_DATA:
      return Object.assign({}, action.payload);
    default:
      return state;
  }
}
